<template>
</template>

<script>
  import { Toast } from 'vant';
  export default {
    name: "index",
    data(){
      return{
        balanceTime:null,
        csjj:true
      }
    },
    created() {
      let that = this

      that.timer = setTimeout(function(){
        that.cs()
      },200);
    },
    beforeDestroy() {
      if(this.timer) { //如果定时器还在运行 或者直接关闭，不用判断
        clearInterval(this.timer); //关闭
      }
    },
    methods:{
      cs(){
        localStorage.setItem('backstage1','true')
        localStorage.setItem('backstage','true')
        this.$router.push({
          name:'market',
        })
      }
    }
  }
</script>

<style scoped>
  .blackClass{
    width: 50px;
    height: 50px;
    z-index: 99;
    background-color: #000;
  }
</style>



